import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { MobileComponent } from './mobile/mobile.component';
import { ElectronicComponent } from './electronic/electronic.component';
import { BooksComponent } from './books/books.component';
import { ComputerComponent } from './computer/computer.component';
import { HomeComponent } from './home/home.component';
import { PowerBankComponent } from './power-bank/power-bank.component';
import { MobileAccessoriesComponent } from './mobile-accessories/mobile-accessories.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';



const appRoutes: Routes = [
  { path: '', component: HomeComponent } ,
  { path: 'home', component: HomeComponent } ,
  { path: 'mobile', component: MobileComponent ,
    children: [ {path: 'power-bank', component: PowerBankComponent},
                {path: 'mobile-accessories', component: MobileAccessoriesComponent}]
          } ,
  { path: 'electronics', component: ElectronicComponent } ,
  { path: 'books', component: BooksComponent } ,
  { path: 'computer', component: ComputerComponent },
  { path: '**', component: PageNotFoundComponent } ,
];
@NgModule({
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
