import { MobileDataType } from './mobile/mobile-var';
import { Injectable } from '@angular/core';
import { Http, HttpModule } from '@angular/http';
import { map } from 'rxjs/operators';

@Injectable()
export class MobDataService {
  constructor(private http: Http) {}
  getMobParts() {
    //  return MOBILE;
    return this.http
      .get('assets/data/APIdata.json')
      .pipe(map(response => <MobileDataType[]>response.json().data));
  }
}
