import { Component, OnInit } from '@angular/core';
import { electronic } from './electronic-var';
import { ELECTRONIC } from './data';

@Component({
  selector: 'app-electronic',
  templateUrl: './electronic.component.html',
  styleUrls: ['./electronic.component.css']
})
export class ElectronicComponent {
 electronics:electronic[];
 lestAside: any;
 ngOnInit(){
    this.electronics=ELECTRONIC;
    var url = window.location.href;
    let index = url.lastIndexOf("/");
    let pageurl = url.substring(index + 1, url.length);
    if (pageurl === "") {
      this.lestAside = false;

    } else {
      this.lestAside = true;
  
    }
  }
 totalelectronic(){
 let sum =0;
  for(let electronic of this.electronics){
       sum += electronic.inStock;     
  }
  return sum;
  }

}
