import { Component, OnInit } from '@angular/core';
import { MobileDataType } from './mobile-var';
import { MOBILE } from './data';
import { MobDataService } from '../mob-data.service';






@Component({
  selector: 'app-mobile',
  templateUrl: './mobile.component.html',
  styleUrls: ['./mobile.component.css']
})
export class MobileComponent implements OnInit {
  lestAside: any;
  constructor(private mobDataServices: MobDataService) { }
  mobiles: MobileDataType[];
  ngOnInit() {
    // this.mobiles = MOBILE;

    // let mobDataServices = new MobDataServices();
    //  this.mobiles = mobDataServices.getMobParts();
    this.mobDataServices
      .getMobParts()
      .subscribe(mobilesn => this.mobiles = mobilesn);

    /* for aside */
    const url = window.location.href;
    const index = url.lastIndexOf('/');
    const pageurl = url.substring(index + 1, url.length);
    if (pageurl === '' || pageurl === '#.') {
      this.lestAside = false;
    } else {
      this.lestAside = true;
    }
  }
  totalmobiles() {
    let sum = 0;
    for (const mobile of this.mobiles) {
      sum += mobile.inStock;
    }
    return sum;
  }
  upQuantity(mobile) {
    if (mobile.quty < mobile.inStock) {
      mobile.quty++;
    }
  }
  dwQuantity(mobile) {
    if (mobile.quty !== 0) {
      mobile.quty--;
    }
  }
}
