import { Component, OnInit } from '@angular/core';
import { computer } from './computer-var';
import { COMPUTER } from './data';

@Component({
  selector: 'app-computer',
  templateUrl: './computer.component.html',
  styleUrls: ['./computer.component.css']
})
export class ComputerComponent {
	computers:computer[];
    	ngOnInit() {
      		this.computers=COMPUTER;
    	}
    totalcomputers(){
    let sum =0;
    	for(let computer of this.computers){
        	sum += computer.inStock;     
    	}
    return sum;
	}
	upQuantity(computer){
  	if(computer.quty < computer.inStock){
    	computer.quty++;
  	}
	}
	dwQuantity(computer) {
  	if(computer.quty!=0){
    	computer.quty--;
  	}
	}
}

